@media screen and (max-width: 834px) {
  .all-components {
    display: none;
  }
  .dashboard {
    padding: 0 1.5em;
  }
  nav {
    padding: 0.5em 1.5em !important;
  }
  .logo h1 {
    font-size: 18px;
  }
  .show-comp {
    width: 100%!important;
    box-sizing: border-box;
}
}
@media screen and (max-width: 660px) {
  nav ul {
    display: none !important;
  }
  .resposive-nav {
    display: block;
    width: 100vw;
    height: 100vh;
    background: white;
    left: 0px;
    top: 37px;
  }
  .resposive-navbar::before {
    content: "\2630";
  }
  .expand-navbar::before {
    content: "\2715";
  }
}
@media screen and (max-width: 574px) {
  .import-code,
  .code-snip {
    flex-wrap: wrap;
  }
  nav {
    padding: 0 !important;
  }
  .ccc {
    padding: 0.5em 1.5em !important;
    background-color: black;
    box-sizing: border-box;
  }
  .first,
  .second {
    flex-direction: column;
  }
  body {
    background-position: top;
  }
  .heading h1 {
    margin: 0;
  }
  .hr-align {
    margin: 0;
  }
  .circle-custom::before {
    content: "";
    display: none;
  }
}

@media screen and (max-width: 470px) {
  .show-comp {
    /* width: 100vw; */
    padding: 0 1em;
  }
  .dashboard {
    padding: 0 !important;
  }
  .preview-show {
    padding: 1.2em;
    margin-bottom: 1.9em;
  }
}
