.hr-align {
  border-color: hsl(240 5% 26%);
  border-top-width: 1px;
  margin: 1em 0;
}
.dashboard {
  flex-wrap: wrap;
  margin-top: 3em;
}
.first,
.second {
  margin-bottom: 1em;
}
.mb-1 {
  margin-bottom: 1em;
}
.first div,
.second div {
  color: aquamarine;
}
.show-comp {
  width: 70vw;
}
.all-components {
width: 20vw;
}
.import-code,
.code-snip {
  overflow: scroll;
}
.how-to-import h2{
  margin: 0.5em 0;
}
.circle-custom::before {
  content: "";
  display: inline-flex;
  margin-right: 14px;
  width: 5px;
  height: 5px;
  border-radius: 302px;
  background-color: #858585;
}
.import-align {
  padding: 12px 20px;
  background: #181818;
  font-size: 14px;
  margin-top: 2.2em;
  margin-bottom: 2.2em;
}
.import-color {
  color: #c678dd;
}
.green-color {
  color: #90b873;
}
.preview-code div h4 {
  font-weight: 400;
  color: #8f8f97;
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
}
.bottom-border {
  border-bottom: 2px solid white;
}
.preview-show {
  border: 1px solid #373737;
  padding: 2em;
  border-radius: 12px;
  margin-top: 1em;
  background-color: #060505;
  margin-bottom: 2.9em;
}
.iframe {
  width: 100%;
  height: 500px;
  border: 0;
  border-radius: 4px;
  overflow: hidden;
}
.custom-blue{
    color: #61afef;
}
.custom-codeColor{
  color:  #cbaa6d;
}
.custom-lightBlue{
    color: #91b4d5;
}
.code-snip{
    flex-direction: column;
    gap: 2px;
    padding-top: 20px;
    padding-left: 20px;
    background-color: #181818;
}
.preview , .code:hover{
cursor: pointer;
}

.max-Layout{
  margin-bottom: 2em;
}